.bx-ui-smiles-box {
	display: flex;
	flex-direction: column;
    flex-wrap: nowrap;
    height: 100%;
}

.bx-ui-smiles-elements-wrap {
	display: flex;
	flex: 1 1 auto;
	overflow: hidden;
	overflow-y: auto;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
}

.bx-ui-smiles-elements {
	display: inline-block;
}

.bx-ui-smiles-smile {
	display: flex;
	min-height: 40px;
	min-width: 40px;
	max-height: 95px;
	max-width: 96px;
	padding: 7px;
	cursor: pointer;
	align-items: center;
    justify-content: center;
}

.bx-ui-smiles-smile-icon {
	display: inline-block;
	object-fit: contain;
	max-height: 95px;
	max-width: 95px;
}

.bx-ui-smiles-sets {
	display: flex;
	flex: 0 0 45px;
	flex-wrap: wrap;
	justify-content: center;
	height: 45px;
	background-color: #f7f7f7;
    border-top: 1px solid #f1eded;
}

.bx-ui-smiles-set {
	display: flex;
	height: 42px;
	width: 42px;
	object-fit: contain;
	flex: 0 0 42px;
	border-bottom: 2px solid  #f7f7f7;
	box-sizing: border-box;
	padding: 5px;
	cursor: pointer;
	transition: all .2s;
}

.bx-ui-smiles-set-icon {
	display: inline-block;
	width: 100%;
}

